.deleteModal {
  position: absolute;
  bottom: 0;
  left: 0;
  min-width: 100%;
  min-height: calc(100% - 60px);
  transform: scale(0);
  transition: all 0.35s ease-in-out;
}

.test {
  transform: scale(1);
  transition: all 0.35s ease-in-out;
}

.modalBg {
  background: linear-gradient(
    0deg,
    rgba(83, 61, 186, 1) 0%,
    rgba(80, 48, 154, 1) 43.14%,
    rgba(106, 70, 165, 1) 73.27%,
    rgba(133, 93, 175, 1) 120.03%
  );
  box-shadow: 0px 4px 60px 0px #00000040;
  backdrop-filter: blur(50px);
  width: 100%;
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.modalContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}

.modalContent p {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.modalContent .buttonsWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

@media screen and (min-width: 768px) {
  .deleteModal {
    background-color: #220d5b3b;
    backdrop-filter: blur(3px);
    min-width: 100%;
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .modalBg {
    width: 533px;
    height: 447px;
    border-radius: 8px;
    background: linear-gradient(
      0deg,
      rgba(83, 61, 186, 0.7) 0%,
      rgba(80, 48, 154, 0.7) 43.14%,
      rgba(106, 70, 165, 0.525) 73.27%,
      rgba(133, 93, 175, 0.133) 120.03%
    );
    background: linear-gradient(
      0deg,
      rgba(83, 61, 186, 1) 0%,
      rgba(80, 48, 154, 1) 43.14%,
      rgba(106, 70, 165, 1) 73.27%,
      rgba(133, 93, 175, 1) 120.03%
    );
    background: rgba(82, 59, 126, 1);
  }
}
